import React, { useState, useEffect } from 'react';
import { Scale, Calculator, HelpCircle, Building2, ArrowRight } from 'lucide-react';
import { motion } from 'framer-motion';
import { Link } from 'react-router-dom';
import { SEO } from '../SEO';
import { Breadcrumb } from '../Breadcrumb';
import { FAQ } from '../FAQ';
import { AppPromoBanner } from '../AppPromoBanner';
import { FAQItem } from '../../types';

const SIMPLES_VS_PRESUMIDO_FAQS: FAQItem[] = [
    {
        question: "Qual a diferença básica?",
        answer: "No Simples Nacional, você paga uma guia única (DAS) com alíquota progressiva sobre o faturamento. No Lucro Presumido, os impostos são pagos separadamente (PIS, COFINS, IRPJ, CSLL, ISS/ICMS) e as alíquotas variam conforme a atividade."
    },
    {
        question: "Quando o Simples vale a pena?",
        answer: "Geralmente para empresas com faturamento menor e folha de pagamento alta (Anexo III ou V com Fator R), pois o Simples isenta a cota patronal do INSS (20%)."
    },
    {
        question: "Quando o Presumido vale a pena?",
        answer: "Para empresas com margem de lucro alta, faturamento elevado (próximo ao teto do Simples) ou atividades com alíquotas muito altas no Simples (Anexo V sem Fator R)."
    }
];

export function SimplesVsPresumidoPage() {
    const [revenue, setRevenue] = useState('');
    const [payroll, setPayroll] = useState('');
    const [activity, setActivity] = useState('servicos'); // servicos, comercio
    const [result, setResult] = useState<{ simples: number; presumido: number; bestOption: string } | null>(null);

    const calculate = () => {
        const rev = parseFloat(revenue.replace(/\./g, '').replace(',', '.') || '0');
        const pay = parseFloat(payroll.replace(/\./g, '').replace(',', '.') || '0');

        if (rev === 0) {
            setResult(null);
            return;
        }

        // --- Simples Nacional Calculation (Simplified) ---
        // Using 2024 Anexos approximations
        let simplesTax = 0;

        if (activity === 'comercio') {
            // Anexo I
            if (rev <= 180000 / 12) simplesTax = rev * 0.04;
            else if (rev <= 360000 / 12) simplesTax = rev * 0.073; // Effective rate approx
            else simplesTax = rev * 0.095; // Simplified average for higher tiers
        } else {
            // Serviços (Anexo III or V)
            // Factor R check
            const factorR = pay / rev;
            if (factorR >= 0.28) {
                // Anexo III (Cheaper)
                if (rev <= 180000 / 12) simplesTax = rev * 0.06;
                else if (rev <= 360000 / 12) simplesTax = rev * 0.112;
                else simplesTax = rev * 0.135;
            } else {
                // Anexo V (Expensive)
                if (rev <= 180000 / 12) simplesTax = rev * 0.155;
                else simplesTax = rev * 0.18;
            }
        }

        // --- Lucro Presumido Calculation ---
        let presumidoTax = 0;
        // PIS (0.65%) + COFINS (3.00%) = 3.65%
        presumidoTax += rev * 0.0365;

        // IRPJ + CSLL (Presumed Profit Base)
        // Serviços: 32% base. Comércio: 8% base (IRPJ) / 12% base (CSLL)
        // Simplified: Approx 11.33% total for Services, 5.93% for Commerce on Revenue
        if (activity === 'servicos') {
            presumidoTax += rev * 0.1133;
            // ISS (2% to 5%) - Assuming 5%
            presumidoTax += rev * 0.05;
        } else {
            presumidoTax += rev * 0.0593;
            // ICMS (Variable, say 18% with credits... hard to estimate. Let's assume effective 4% for comparison)
            presumidoTax += rev * 0.04;
        }

        // INSS Patronal (20% on Payroll) - Only for Presumido
        presumidoTax += pay * 0.20; // Plus RAT/Sistema S ~ 5-8% more... simplified to just 20% patronal

        setResult({
            simples: simplesTax,
            presumido: presumidoTax,
            bestOption: simplesTax < presumidoTax ? 'Simples Nacional' : 'Lucro Presumido'
        });
    };

    useEffect(() => {
        calculate();
    }, [revenue, payroll, activity]);

    const formatCurrency = (value: string) => {
        const number = value.replace(/\D/g, '');
        return (Number(number) / 100).toLocaleString('pt-BR', { minimumFractionDigits: 2 });
    };

    const handleCurrencyInput = (value: string, setter: (value: string) => void) => {
        setter(formatCurrency(value));
    };

    const schema = {
        "@context": "https://schema.org",
        "@type": "WebApplication",
        "name": "Calculadora Simples Nacional vs Lucro Presumido",
        "description": "Qual o melhor regime tributário para sua empresa? Compare os impostos e economize dinheiro.",
        "applicationCategory": "BusinessApplication",
        "operatingSystem": "Any",
        "offers": {
            "@type": "Offer",
            "price": "0",
            "priceCurrency": "BRL"
        }
    };

    return (
        <section className="relative min-h-screen pt-32 pb-24 px-4 overflow-hidden">
            <SEO
                title="Simples Nacional ou Lucro Presumido? Comparador 2025"
                description="Faça a escolha certa. Compare os impostos do Simples Nacional e Lucro Presumido e descubra qual regime tributário é mais barato para sua empresa."
                canonical="/calculadoras/simples-vs-presumido"
            />
            <script type="application/ld+json">
                {JSON.stringify(schema)}
            </script>
            <script type="application/ld+json">
                {JSON.stringify({
                    "@context": "https://schema.org",
                    "@type": "FAQPage",
                    "mainEntity": SIMPLES_VS_PRESUMIDO_FAQS.map(faq => ({
                        "@type": "Question",
                        "name": faq.question,
                        "acceptedAnswer": {
                            "@type": "Answer",
                            "text": faq.answer
                        }
                    }))
                })}
            </script>

            {/* Background Orbs */}
            <div className="absolute top-[-10%] left-[-10%] w-[800px] h-[800px] bg-amber-500/5 rounded-full blur-[120px] pointer-events-none mix-blend-screen" />
            <div className="absolute bottom-[10%] right-[-10%] w-[600px] h-[600px] bg-blue-500/10 rounded-full blur-[120px] pointer-events-none mix-blend-screen" />

            <div className="max-w-7xl mx-auto relative z-10">
                <div className="mb-8">
                    <Breadcrumb items={[
                        { label: 'Calculadoras', href: '/calculadoras' },
                        { label: 'Simples vs Presumido', href: '/calculadoras/simples-vs-presumido' }
                    ]} />

                    <motion.div
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ duration: 0.6 }}
                        className="text-center mb-12"
                    >
                        <div className="inline-flex items-center gap-2 px-4 py-2 rounded-full bg-white/5 border border-white/10 mb-6 backdrop-blur-sm">
                            <Scale className="w-4 h-4 text-amber-500" />
                            <span className="text-sm text-gray-300">Empresariais</span>
                        </div>
                        <h1 className="text-4xl md:text-5xl font-bold text-white mb-6 tracking-tight">
                            Simples Nacional ou <span className="text-transparent bg-clip-text bg-gradient-to-r from-amber-500 to-blue-500">Lucro Presumido?</span>
                        </h1>
                        <p className="text-lg text-gray-400 max-w-2xl mx-auto hidden">
                            {/* Description moved below calculator */}
                        </p>
                    </motion.div>
                </div>

                <div className="grid lg:grid-cols-12 gap-8 mb-24">
                    {/* Calculator */}
                    <motion.div
                        initial={{ opacity: 0, x: -20 }}
                        animate={{ opacity: 1, x: 0 }}
                        transition={{ duration: 0.6, delay: 0.2 }}
                        className="lg:col-span-7"
                    >
                        <div className="bg-[#1a1a1a]/50 backdrop-blur-xl border border-white/5 rounded-3xl p-6 md:p-8 min-h-[600px]">
                            <div className="flex items-center justify-between mb-8">
                                <h2 className="text-xl font-semibold flex items-center gap-2 text-white">
                                    <Calculator className="w-5 h-5 text-amber-500" />
                                    Comparar Regimes
                                </h2>
                            </div>

                            <div className="space-y-6">
                                <div className="space-y-2">
                                    <label className="text-sm text-gray-400">Faturamento Mensal</label>
                                    <div className="relative">
                                        <span className="absolute left-4 top-1/2 -translate-y-1/2 text-gray-500">R$</span>
                                        <input
                                            type="text"
                                            value={revenue}
                                            onChange={(e) => handleCurrencyInput(e.target.value, setRevenue)}
                                            className="w-full bg-[#0a0a0a] border border-white/10 rounded-xl py-3 pl-10 pr-4 text-white focus:outline-none focus:border-amber-500/50 transition-all"
                                            placeholder="0,00"
                                        />
                                    </div>
                                </div>

                                <div className="grid md:grid-cols-2 gap-6">
                                    <div className="space-y-2">
                                        <label className="text-sm text-gray-400">Folha de Pagamento (Mensal)</label>
                                        <div className="relative">
                                            <span className="absolute left-4 top-1/2 -translate-y-1/2 text-gray-500">R$</span>
                                            <input
                                                type="text"
                                                value={payroll}
                                                onChange={(e) => handleCurrencyInput(e.target.value, setPayroll)}
                                                className="w-full bg-[#0a0a0a] border border-white/10 rounded-xl py-3 pl-10 pr-4 text-white focus:outline-none focus:border-amber-500/50 transition-all"
                                                placeholder="0,00"
                                            />
                                        </div>
                                    </div>
                                    <div className="space-y-2">
                                        <label className="text-sm text-gray-400">Atividade Principal</label>
                                        <select
                                            value={activity}
                                            onChange={(e) => setActivity(e.target.value)}
                                            className="w-full bg-[#0a0a0a] border border-white/10 rounded-xl py-3 px-4 text-white focus:outline-none focus:border-amber-500/50 transition-all"
                                        >
                                            <option value="servicos">Prestação de Serviços</option>
                                            <option value="comercio">Comércio</option>
                                        </select>
                                    </div>
                                </div>

                                <div className="pt-6 border-t border-white/5">
                                    <div className="bg-amber-500/10 p-6 rounded-2xl border border-amber-500/20 text-center mb-4">
                                        <span className="text-sm text-amber-400 block mb-2">Melhor Opção Estimada</span>
                                        <span className="text-3xl font-bold text-white">
                                            {result ? result.bestOption : '---'}
                                        </span>
                                    </div>
                                    <div className="grid grid-cols-2 gap-4">
                                        <div className={`p-4 rounded-xl border text-center ${result && result.bestOption === 'Simples Nacional' ? 'bg-green-500/10 border-green-500/20' : 'bg-white/5 border-white/5'}`}>
                                            <span className="text-xs text-gray-400 block mb-1">Simples Nacional</span>
                                            <span className="text-xl font-bold text-white">
                                                {result ? `R$ ${result.simples.toLocaleString('pt-BR', { minimumFractionDigits: 2 })}` : '---'}
                                            </span>
                                        </div>
                                        <div className={`p-4 rounded-xl border text-center ${result && result.bestOption === 'Lucro Presumido' ? 'bg-green-500/10 border-green-500/20' : 'bg-white/5 border-white/5'}`}>
                                            <span className="text-xs text-gray-400 block mb-1">Lucro Presumido</span>
                                            <span className="text-xl font-bold text-white">
                                                {result ? `R$ ${result.presumido.toLocaleString('pt-BR', { minimumFractionDigits: 2 })}` : '---'}
                                            </span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </motion.div>

                    {/* Sidebar Info */}
                    <motion.div
                        initial={{ opacity: 0, x: 20 }}
                        animate={{ opacity: 1, x: 0 }}
                        transition={{ duration: 0.6, delay: 0.4 }}
                        className="lg:col-span-5 space-y-6"
                    >
                        <div className="bg-[#1a1a1a]/50 backdrop-blur-xl border border-white/5 rounded-3xl p-6">
                            <h3 className="text-lg font-semibold mb-4 flex items-center gap-2 text-white">
                                <Building2 className="w-5 h-5 text-amber-500" />
                                Fator R
                            </h3>
                            <div className="space-y-4 text-sm text-gray-400">
                                <p>
                                    Para empresas de serviços no Simples Nacional, a relação entre folha de pagamento e faturamento (Fator R) define o anexo.
                                </p>
                                <ul className="space-y-2 list-disc pl-4">
                                    <li><strong>Fator R {'>'}= 28%:</strong> Anexo III (Alíquotas menores, a partir de 6%).</li>
                                    <li><strong>Fator R {'<'} 28%:</strong> Anexo V (Alíquotas maiores, a partir de 15,5%).</li>
                                </ul>
                                <div className="p-3 rounded-xl bg-white/5 border border-white/5 mt-2">
                                    <strong className="text-white block mb-1">Dica</strong>
                                    Às vezes, aumentar o Pró-Labore para atingir o Fator R de 28% gera uma economia tributária enorme no Simples.
                                </div>
                            </div>
                        </div>
                    </motion.div>
                </div>

                <div className="mt-8 max-w-2xl mx-auto text-lg text-gray-400 text-center mb-12">
                    <p>
                        Compare os regimes tributários e descubra onde sua empresa paga menos impostos.
                    </p>
                </div>

                <FAQ
                    items={SIMPLES_VS_PRESUMIDO_FAQS}
                    title="Dúvidas sobre Regimes Tributários"
                    className="py-12"
                    showSocialProof={false}
                />

                <AppPromoBanner />
            </div>
        </section>
    );
}
